<template lang="pug" src="./assets/tpl/app.pug" />
<style lang="stylus" src="./assets/styl/main.styl" />

<script>
import Top from '@/components/Top'

export default {
  name: 'app',
  data(){
      return{
        categories: []
      }
    },
    created() {
      this.$http.get('/static/data/categories.json').then(response => {
        this.categories = response.body;
      }, response => {
      });
    },
    methods: {
      filterBlog: function(id) {

        this.$emit('check-category', id);
      }
    },
    components: {Top}
}
</script>



