<template lang="pug" src="../assets/tpl/components/top.pug" />

<script>
export default {
  name: 'top',
  props: ['data-type'],
  data () {
    return {
      items: [],
      objType: this.dataType
    }
  },
  mounted() {
    this.$http.get('/static/data/' + this.dataType + '.json').then(response => {
      this.items = response.body.slice(0,5);
      }, response => {
    });
  },
  methods: {
    createLink: function(obj) {
      var link = '/';
      if (this.dataType == 'linch') {
        link += 'linch/'+ obj.id;

      } else {
        link += obj.slug

      }
      return link
    }
  }
}
</script>



