<template lang="pug" src="../assets/tpl/components/comments.pug" />
<style lang="stylus" src="../assets/styl/form.styl" scoped />

<script>
export default {
  name: 'comments',
  data () {
    return {
      comments: [],
      commentForm: {
        name: '',
        text: ''
      }
    }
  },
  created() {
    this.$http.get('/static/data/comments.json').then(response => {
      this.comments = response.body;
      }, response => {
    });
  },
  methods: {
    hexColor: str => {
      var result = 'border-color: #';
      for (var i=str.length-1; i > str.length-4; i--) {
        result += str.charCodeAt(i).toString(16);
      }
      console.log(result)
      return result;
    },
    sendRequest() {
      this.commentForm = {
        name: '',
        text: ''
      }
      alert('sended')
    }
  }
}
</script>



