import Vue from 'vue'
import Router from 'vue-router'
import Blog from '@/components/Blog'
import BlogDetail from '@/components/BlogDetail'
import Linch from '@/components/Linch'
import LinchDetail from '@/components/LinchDetail'

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Blog',
      component: Blog
    },
    {
      path: '/:slug',
      name: 'BlogDetail',
      component: BlogDetail
    },
    {
      path: '/linch',
      name: 'Linch',
      component: Linch
    },
    {
      path: '/linch/:id',
      name: 'LinchDetail',
      component: LinchDetail
    }
  ]
})



