<template lang="pug" src="../assets/tpl/components/blog_detail.pug" />

<script>
import Comments from './Comments'

export default {
  name: 'blogDetail',
  data () {
    return {
      post: {},
    }
  },
  created() {
    this.$http.get('/static/data/blog.json').then(
      response => {
        this.post = response.body.filter(obj => {return obj.slug == this.$route.params.slug})[0];
      },
      response => {}
    );
  },
  components: {
    Comments
  }
}
</script>



