<template lang="pug" src="../assets/tpl/components/linch_detail.pug" />

<script>
import Comments from './Comments'

export default {
  name: 'linchDetail',
  data () {
    return {
      linch: {},
    }
  },
  created() {
    this.$http.get('/static/data/linch.json').then(
      response => {
        this.linch = response.body.filter(obj => {return obj.id == this.$route.params.id})[0];
      },
      response => {}
    );
  },
  components: {
    Comments
  }
}
</script>



