<template lang="pug" src="../assets/tpl/components/linch_list.pug" />
<style lang="stylus" src="../assets/styl/form.styl" scoped />

<script>
export default {
  name: 'linch',
  data () {
    return {
      linches: [],
      linchForm: {
        url: null,
        email:null,
        request:null
      }
    }
  },
  created() {
    this.$http.get('/static/data/linch.json').then(response => {
      this.linches = response.body;
      }, response => {
    });
  },
  methods: {
    bgImgUrl(linch, index) {
      var containerWidth = this.$el.clientWidth;
      var height = 16*10;
      var imgHeight = height;
      var imgWidth;
      var maskElem = index % 10;

      if (containerWidth <= 425 || maskElem == 0 ) {
        imgWidth = containerWidth;
      } else if (containerWidth <=736 || [8,9].includes(maskElem)) {
        imgWidth = parseInt(containerWidth / 2);
      } else if (maskElem == 3) {
          imgWidth = parseInt(containerWidth * 2 / 3);
      } else {
        imgWidth = parseInt(containerWidth / 3);
        if (maskElem == 4) {
          imgHeight = 2 * (height + 16);
        }
      }

      var imgUrl = 'http://lorempixel.com/'+ imgWidth +'/'+ imgHeight +'/sports/';
      return 'background-image: url('+ imgUrl + ')'
    },
    sendRequest() {
      this.linchForm = {
        url: null,
        email:null,
        request:null
      }
      alert('sended')
    }
  }
}
</script>



