<template lang="pug" src="../assets/tpl/components/blog_list.pug" />

<script>
export default {
  name: 'blog',
  data () {
    return {
      posts: []
    }
  },
  created() {
    this.$http.get('/static/data/blog.json').then(response => {
      this.posts = response.body;
      var categories = this.$parent.categories;
      for (var post of this.posts) {
        post.category = categories.filter(function(obj){
          return obj.id == post.category;
        })[0];
      }
      }, response => {
    });
  },
  mounted() {
    this.$parent.$on('check-category', category => {
      this.posts = this.posts.filter(obj => {
        return obj.category.id == category;
      });
    });
  }
}
</script>



